@import '../base/index';
.main{
    width: 100%;
    height: 100%;
  
    @include grid(2, 0px);
    @include telefono{
        grid: none;

        height: auto;
      position: absolute;
      top: 20%;
    }
}

.first-column{
    background-image: url('../img/uno.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
   
    @include telefono{
        background-image: none;
        display: none;
    }
}
.second-column{
    padding: 1rem 2rem;
   height: 100%;
    @include telefono{
        align-self: center;
    }
    
    h1, h2{
        font-weight: 100;
    }
    fieldset{
        border: none;
        text-align: center;
        
    }
    .campos {
        display: flex;
        align-content: center;
        justify-content: space-between;
        margin-bottom: 1rem;
        @include desktop{
            flex-direction: column;
        }

        label{
            align-self: center;
            width: 7rem;
            text-align: center;
        }
      
        *{
            margin: 0 1rem;
        }
    }
    
}
.options{
    display: flex;
    justify-content: space-between;
    padding: .5rem;
    
}
.options-msg{
    cursor: pointer;
    opacity: 0.6;
   
    font-size: 1.5rem;
    color: $gris;
    &:hover{
        text-decoration: underline;
    }
}
