@use 'base/mixins' as m;
@use 'base/variables' as v;
.btnBlue{
    @include m.boton(v.$azul, v.$blanco);
    @include m.tablet{
        width: 100%;
    }
}
.btnRed{
    margin: 0;
    @include m.boton(v.$rojo, v.$blanco);
    @include m.tablet{
        width: 100%;
    }
}
.btnGreen{
    margin: 0;
    @include m.boton(v.$verde, v.$blanco);
    @include m.tablet{
        width: 100%;
    }
    margin-left: .5rem;
}