@use 'variables' as v;
@use 'mixins' as m;
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100vh;
}
body {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
     background-color: v.$negro;
    color: v.$blanco;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
*, *:before, *:after {
    box-sizing: inherit;
}

.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
input{
   flex: 1;
    border-radius: 1rem;
    padding: .7rem;
    &:focus{
        outline: none;
    }
}