.main {
  width: 100%;
  height: 100%;
  display: grid;
  gap: 0px;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 580px) {
  .main {
    grid: none;
    height: auto;
    position: absolute;
    top: 20%;
  }
}

.first-column {
  background-image: url("../img/uno.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 580px) {
  .first-column {
    background-image: none;
    display: none;
  }
}

.second-column {
  padding: 1rem 2rem;
  height: 100%;
}
@media (max-width: 580px) {
  .second-column {
    align-self: center;
  }
}
.second-column h1, .second-column h2 {
  font-weight: 100;
}
.second-column fieldset {
  border: none;
  text-align: center;
}
.second-column .campos {
  display: flex;
  align-content: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}
@media (max-width: 1254px) {
  .second-column .campos {
    flex-direction: column;
  }
}
.second-column .campos label {
  align-self: center;
  width: 7rem;
  text-align: center;
}
.second-column .campos * {
  margin: 0 1rem;
}

.options {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
}

.options-msg {
  cursor: pointer;
  opacity: 0.6;
  font-size: 1.5rem;
  color: #e1e1e1;
}
.options-msg:hover {
  text-decoration: underline;
}

.btnBlue {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition: background-color 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (max-width: 1100px) {
  .btnBlue {
    width: auto;
  }
}
.btnBlue:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
@media (max-width: 1100px) {
  .btnBlue {
    width: 100%;
  }
}

.btnRed {
  margin: 0;
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition: background-color 0.3s;
  text-align: center;
