  margin: 5rem 0;
}
@media (max-width: 1100px) {
  .btnRed {
    width: auto;
  }
}
.btnRed:hover {
  background-color: #980000;
  cursor: pointer;
}
@media (max-width: 1100px) {
  .btnRed {
    width: 100%;
  }
}

.btnGreen {
  margin: 0;
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
