  display: flex;
}
.barras span {
  width: 3rem;
  height: 1rem;
  margin: 1rem;
  flex-grow: 1;
  background-color: #FFFFFF;
  cursor: pointer;
}
.barras span:hover {
  background-color: #0da6f3;
}

.servicios {
  display: flex;
}
.servicios * {
  flex: 1;
  text-align: center;
}

.text-center {
  text-align: center;
  text-shadow: 0.1rem 0.5rem 1rem #cb0000;
}

input:disabled {
  background-color: #aaa;
  cursor: not-allowed;
}

.tabs {
  display: flex;
}
.tabs * {
  flex: 1;
  margin: 1rem;
  cursor: pointer;
  padding: 1rem;
  border-radius: 1rem;
  border: none;
  background-color: #ccc;
  transition: all 0.5s;
  font-weight: bold;
}
.tabs *:hover {
  background-color: #0b96dc;
  box-shadow: 1rem 1rem 10rem #0da6f3;
  color: #FFFFFF;
}

.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.ocultar {
  display: none;
}

.mostrar {
  display: block;
}

