/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100vh;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

input {
  flex: 1;
  border-radius: 1rem;
  padding: 0.7rem;
}

input:focus {
  outline: none;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 500;
  color: #FFFFFF;
  text-align: center;
  margin: 1rem 0;
}

h1 {
  font-size: 3.7rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.barra-servicios {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 580px) {
  .barra-servicios {
    flex-direction: column;
  }
  .barra-servicios * {
    margin: 1rem 0;
  }
}

.serviceContainer {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 580px) {
  .serviceContainer {
    grid-template-columns: 1fr;
  }
}

.citas-admin {
  max-height: 100rem;
  overflow: scroll;
}

.boton-eliminar {
  background: none;
  color: #fff;
  border: none;
  cursor: pointer;
}

.servicio.admin {
  color: #000;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1rem;
}

button .alerta.errores {
  color: #fff;
}

.form-servicios {
  display: flex;
  justify-content: center;
  text-align: center;
}

.campo {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
}
.campo label {
  width: 10rem;
  text-align: center;
  margin-right: 0.5rem;
  padding: 1rem;
}
.campo input {
  flex-grow: 1;
}
@media (max-width: 580px) {
  .campo {
    flex-direction: column;
  }
}