@import '../base/index';

.barras{
    display: flex;
    span{
       width: 3rem;
       height: 1rem;
        margin: 1rem;
        flex-grow: 1 ;
        background-color: $blanco;
        cursor: pointer;
        &:hover{
            background-color: $azul;
        }
    }
}
.servicios{
    display: flex;
    *{
        flex: 1;
        text-align: center;
    }

}

.text-center{
    text-align: center;
    text-shadow: .1rem .5rem 1rem $rojo;
}
    input{
        &:disabled{
            background-color: #aaa;
            cursor: not-allowed;
        }
    }

.tabs{
    display: flex;
    *{
        flex: 1;
        margin: 1rem;
        cursor: pointer;
        padding:1rem;
        border-radius: 1rem;
        border: none;
        background-color: #ccc;
        transition: all .5s;
        font-weight: bold;
         &:hover{
            background-color: darken($azul,5%);
            box-shadow: 1rem 1rem 10rem $azul;
            color: $blanco;
        }
         
    }
}
.actual{
    background-color: $blanco;
            color: $azul;
}
.ocultar{
    display: none;
}
.mostrar{
    display: block;
}
.paginacion{
 
    display: flex;
    justify-content: space-between;
    margin: 2rem 0;
  
    .arrows{
       
       @include boton($azul, $blanco);
        text-align: center;
       display: flex;
       justify-content: center;
       align-items: center;
       &.ocultar{
        visibility: hidden;
       }
    }
    
}
