@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 500;
    color: v.$blanco;
    text-align: center;
    margin: 1rem 0;
}

h1 {
    font-size: 3.7rem;
}
h2 {
    font-size: 2.6rem;
}
h3 {
    font-size: 2rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}
