@import '../base/index';
.alerta{
    text-align: center;
    padding: 1rem;
    border-radius: 1rem;
    font-weight: bold;
    &.correcto{
        background-color: $verde;
        box-shadow: .5rem 1rem 7rem $verde;
    }
    &.errores{
        background-color: $rojo;
        box-shadow: .5rem 1rem 7rem $rojo;
    }
    &.repeated{
        background-color: $azul;
        box-shadow: .5rem 1rem 7rem $azul;
    }
}
.msg-cerrado{
    display: none;
    text-align: center;
}
.swal2-html-container{
    font-size: 1.5rem;
    color: #e1e1e1;
    text-align: center;
}
.swal2-title{
    color:'#000'
}
/*BOTON*/
.swal2-styled.swal2-confirm{
    transform: scale(1.5);
}