@use 'base/mixins' as m;
@use 'base/variables' as v;
.listado-servicios{
    @include m.grid(3,.5rem);
    @include m.tablet{
       @include m.grid(2,.5rem);
    }
    
}
.servicio{
    cursor: pointer;
   background-color: v.$blanco;
   text-align: center;
   padding: 1rem;
   border-radius: 1rem;
   margin: 1rem;
   transition: all 0.3s;
   &:hover{
    
    transform: translateY(-1rem);
    box-shadow: 1rem 1rem 2rem v.$azul;
    
    }
    &.selected{
        background-color:darken(v.$blanco, 30%) ;
    }
   
   .producto{
    color: v.$negro;
    font-weight: bold;
   }
   .precio{
    color:  v.$azul;
    font-weight: bold;
    
   }
}