@use 'base/mixins' as m;
@use 'base/variables' as v;
@import '../base/index';
.barra-servicios{
    display: flex;
    justify-content: space-between;
    @include telefono{
        flex-direction: column;
        *{
            margin: 1rem 0
        }
    }
}
.serviceContainer{
    @include grid(3, 10px);
    @include telefono{
        grid-template-columns: 1fr;
    }
}
.citas-admin{
    max-height: 100rem;
    overflow: scroll;
}
.boton-eliminar{
    background: none;
    color:#fff;
    border: none;
    cursor: pointer;
}
.servicio{
   
    &.admin{
        color: #000;
        font-weight: bold;
        margin: 0;
        margin-bottom:1rem ;
    }
}
button .alerta {
    &.errores{
        color:#fff;
    }
}
.form-servicios{
    display: flex;
    justify-content: center;
    text-align: center;
}
// CREAR SERVICIOS
.campo{
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 1rem;
     label{
      width: 10rem;
       text-align: center;
       margin-right: .5rem;
       padding: 1rem;
    }
    input{
      
        flex-grow: 1;
    }
   @include telefono{
    flex-direction: column;
   }
}